import React from 'react';
import { get } from 'lodash';
import { DisplayColumn } from '@jutro/datatable';

export const MappedColumn = () => {
    if (__DEV__) {
        throw new Error('Component <MappedColumn /> should never render');
    }
    return <React.Fragment />;
};

MappedColumn.defaultCell = (row, rowId, { path, visible, valueMap }) => {
    if (visible === false) {
        return null;
    }

    const value = get(row, path);
    const name = valueMap[value];

    if (name === undefined) {
        return DisplayColumn.defaultCell(row, rowId, { path });
    }
    return <span>{name}</span>;
};

MappedColumn.displayName = 'MappedColumn';

MappedColumn.defaultProps = {
    ...DisplayColumn.defaultProps,
    valueMap: {},
    renderCell: MappedColumn.defaultCell,
};
